<template>
    <div class="container">
      <el-row>
        <el-col :span="8">
          <h1 class="title">修改退款单</h1> 
          <label for="">对应退货单id</label><el-input v-model="task.pr_bill_id" placeholder="对应退货单id"></el-input>
          <label for="">退回价值</label><el-input v-model="task.rv_money" placeholder="退回价值"></el-input> 
        </el-col>
      </el-row>
      <div class="footerButton border-shadow">
        <el-button type="primary" @click="submitTask()" style="vertical-align: top">提交</el-button>
      </div>
    </div>
</template>

<script>
    export default {
      name: "addTask",
      data() {
          return { 
            pageName: '添加',
            task: {
              pr_bill_id: "",
              rv_money: "", 
            },
           
            warehouseOptions: [
              {
                label: '国内仓',
                value: '1'
              },
              {
                label: '泰国仓',
                value: '2'
              } 
            ],
            

          }
      },
      methods: {
        submitTask() {
          let url = "http://api.orkowms.me/receivables";
          let params = {};
          let config = {};
          params.pr_bill_id = this.task.pr_bill_id;
          params.rv_money = this.task.rv_money; 
          let that = this; 
          if(that.task.id){
             config = {
              headers : { 
              //  'Content-Type':'application/x-www-form-urlencoded'
              },
            };
             url = "http://api.orkowms.me/receivables/"+this.task.id ; 
          }  
          this.$axios.put(url,params).then((res) => {
             
              that.$message({
                type: 'success',
                message: '更新成功!'
              }); 
            
          }).catch((err) => {
             
              that.$message({
                type: 'error',
                message: '更新失败!'
              }); 
          })
        },
         
      },
      created() {
         let row = this.$route.query;  
          this.task.id = row.id; 
          this.task.pr_bill_id = row.pr_bill_id;
          this.task.rv_money = row.rv_money; 
      }
    }
</script>

<style scoped lang="stylus">
  .container
    .el-row
      margin 30px 30px 0px 30px
    label
      display block
      font-weight bold
      font-size: 20px
      margin: 10px 0
    .title
      margin-bottom: 50px
    .button
      margin-top: 20px
    .upload-img
      display inline-block
      margin-right 30px
    .footerButton
      position absolute
      left: 0
      bottom 0
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;

</style>



